import React, { useState } from 'react';
import JSZip from 'jszip';
import CleanCSS from 'clean-css';
import { useForm } from 'formydable';
import Tooltip from 'Snippets/Tooltip';
import { Modal, Collapse } from 'Snippets/Neumorphism';
import { useLocal } from 'Source/App/Storage/Local';
import { FileGenerator, is } from 'Common/Utils';

export const Actions = () => {
	const { getLocalAll } = useLocal();
	const [actions, setActions] = useState(false);
	const [reset, setReset] = useState(false);
	const [save, setSave] = useState(false);
	const [upload, setUpload] = useState(false);
	const actionHandler = (e) => {
		const target = e.currentTarget;
		clearTimeout(target.cleanup);
		target.cleanup = setTimeout(() => target.blur(), 600);
		setActions((prev) => !prev);
	};
	const resetCloseHandler = () => setReset(false);
	const saveCloseHandler = () => setSave(false);
	const uploadCloseHandler = () => setUpload(false);
	const resetSettingHandler = () => {
		window.localStorage.clear();
		window.location.reload();
	};
	const saveAllSettingHandler = () => {
		const locals = getLocalAll();
		FileGenerator({
			filename: 'mytabworks.neumorphism-settings',
			contentType: 'application/json',
			body: JSON.stringify(locals),
		});
		saveCloseHandler();
	};
	const handleDownload = () => {
		const stylesheet = document.getElementById('stylesheet').innerText
		const minifystyle = new CleanCSS().minify(stylesheet).styles
		const scripts = process.env.NSCRIPTS
		const minfiyscripts = process.env.NSCRIPTSMIN 
		const zip = new JSZip();
		const folder = zip.folder("neumorphism"); 
		const dist = folder.folder("dist")
		const css = dist.folder("css")
		const js = dist.folder("js")
		css.file("neumorphism.css", stylesheet)
		css.file("neumorphism.min.css", minifystyle)
		js.file("neumorphism.js", scripts)
		js.file("neumorphism.min.js", minfiyscripts)
		zip.generateAsync({type:"base64"})
		.then(function(content) {
			// see FileSaver.js
			FileGenerator({
				filename: 'mytabworks-neumorphism.zip',
				contentType: 'application/zip',
				type: 'base64',
				body: content,
			});
		});
	}
	return (
		<>
			<div className='d-flex flex-spb-x mb-3'>
				<Tooltip variant='dark' message='actions for settings'>
					<button className='n-btn' onClick={actionHandler}>
						Actions
					</button>
				</Tooltip>
				<Tooltip variant='dark' message='download css'>
					<button className='n-btn' onClick={handleDownload}>Download</button>
				</Tooltip>
			</div>
			<Collapse show={actions}>
				<Tooltip variant='dark' message='reset all settings'>
					<button
						className='n-btn d-block w-occupy mb-3 mt-1'
						onClick={() => setReset(true)}>
						Reset Settings
					</button>
				</Tooltip>
				<Tooltip variant='dark' message='save all settings' placement='right'>
					<button
						className='n-btn d-block w-occupy mb-3'
						onClick={() => setSave(true)}>
						Download Settings
					</button>
				</Tooltip>
				<Tooltip
					variant='dark'
					message='upload settings'
					placement='bottom-right'>
					<button
						className='n-btn d-block w-occupy mb-1'
						onClick={() => setUpload(true)}>
						Upload Settings
					</button>
				</Tooltip>
			</Collapse>
			<Modal show={upload} size='sm' onHide={uploadCloseHandler}>
				<Modal.Title onHide={uploadCloseHandler}>Upload settings</Modal.Title>
				<Modal.Body>
					<UploadSetting />
				</Modal.Body>
				<Modal.Footer>
					<button className='n-btn' onClick={uploadCloseHandler}>
						No
					</button>
					<button className='n-btn' form='upload-setting'>
						Yes
					</button>
				</Modal.Footer>
			</Modal>

			<Modal show={reset} size='sm' onHide={resetCloseHandler}>
				<Modal.Title onHide={resetCloseHandler}>Reset settings</Modal.Title>
				<Modal.Body>
					<p>
						all of this settings will be reset back to default, are you sure?
					</p>
				</Modal.Body>
				<Modal.Footer>
					<button className='n-btn' onClick={resetCloseHandler}>
						No
					</button>
					<button className='n-btn' onClick={resetSettingHandler}>
						Yes
					</button>
				</Modal.Footer>
			</Modal>
			<Modal show={save} size='sm' onHide={saveCloseHandler}>
				<Modal.Title onHide={saveCloseHandler}>Download settings</Modal.Title>
				<Modal.Body>
					<p>your are downloading this settings, do we preceed?</p>
				</Modal.Body>
				<Modal.Footer>
					<button className='n-btn' onClick={saveCloseHandler}>
						No
					</button>
					<button className='n-btn' onClick={saveAllSettingHandler}>
						Yes
					</button>
				</Modal.Footer>
			</Modal>
		</>
	);
};

const UploadSetting = () => {
	const { formState, formUpdate, formSubmit } = useForm({
		'setting-upload': {
			label: 'Setting Upload',
			rules: 'required|mimes:neumorphism-settings',
		},
	});
	const uploadstate = formState('setting-upload');
	const uploadSettingHandler = formSubmit((e) => {
        if(!e.isReady()) return
		const fr = new FileReader();

		fr.addEventListener('load', () => {
			const uploadedSettings = JSON.parse(fr.result);
			Object.keys(uploadedSettings).forEach((key) => {
				if (is.obj(uploadedSettings[key])) {
					window.localStorage.setItem(
						key,
						JSON.stringify(uploadedSettings[key])
					);
				}
			});
			window.location.reload();
		});

		fr.readAsText(e.target[0].files[0]);
	});
	return (
		<form id='upload-setting' onSubmit={uploadSettingHandler}>
			{uploadstate.isInvalid && ( 
                <small style={{ color: '#db1585' }}>
                    {uploadstate.failedIn === 'mimes'
                        ? 'This only allow .neumorphism-settings file'
                        : 'This field is required' }
                </small>
			)}
			<input
				className='n-form-control mb-3 mt-1'
				type='file'
				name='setting-upload'
				onChange={formUpdate}
				style={uploadstate.isInvalid ? { borderColor: '#db1585' } : undefined}
			/>
			<Collapse show={uploadstate.isValidated && !uploadstate.isInvalid}>
				<p>
					uploading this setting will deliberatly replace the current setting permanently,
					are you going to proceed?
				</p>
			</Collapse>
		</form>
	);
};
