import React from 'react';
import { Introductory } from 'Templates';
import { useLocal } from 'Source/App/Storage/Local';
import { localkey } from 'Source/App/Main/local-config';

export const Introduction = () => {
	const { getLocal } = useLocal(localkey.general);
	const { lightSource } = getLocal();

	return (
		<Introductory
			title='Neumorphism Project's
			descriptions={[
				`CSS framework and a styling tool that let you customize a theme of your own unique colors and component texture of neu-skeumorphic design for your web-application UI.`
			]}
			link='/neumorphism/button'
			brandImage={<img
				alt="beard-guy"
				className={`hero-icon ${lightSource} n-float-low`}
				src='/images/icons/icon-384x384.png'
			/>}
			columns={[
				{
					title: 'What is neumoprhism?',
					description: `Neumorphism is a visual interface that allows achieving a soft
					and extruded silicon look. it is also known as Soft UI.
					Neumorphism is all about subtle contrast and solid colors.`,
				},
				{
					title: 'Why Neumorphism Project?',
					description: `This css semi-framework gives you the basic necessities of neu-skeumorphic design that lets you customize and
					generate the components on your own prefered theme color, shadows intensity, and light direction.`,
				},
			]}
		/>
	);
};
