import React from 'react';
import { useHistory, useLocation, Link, Route, RouteNotFound } from 'reaouter';
import { useLocal } from 'Source/App/Storage/Local';
import { isColorDark, colorShade } from 'Common/Utils';
import { Sidebar } from 'Templates';
import { DocumentationFacade } from 'Source/App/Main/Facades';
import { Actions } from './Actions';
import { localkey } from 'Source/App/Main/local-config';
import {
	Introduction,
	Button,
	Card,
	Collapse,
	Dropdown,
	Forms,
	Freestyle,
	InputGroup,
	Modal,
	ProgressBar,
	Navs,
	Table,
	Utilities,
} from './Topics';
import { PageNotFound } from '../';

const NeumorphismPage = () => {
	const location = useLocation();
	const history = useHistory();
	return (
		<>
			<Route path={`${location.path}`} render={<Introduction />} />
			{history.url !== location.url && (
				<>
					<NeumorphismPageSideBar {...location} />
					<NeumorphismPageMain {...location} />
				</>
			)}
		</>
	);
};

const NeumorphismPageMain = ({ path, url }) => (
	<DocumentationFacade>
		<Route path={`${path}/button`} render={<Button />} />
		<Route path={`${path}/card`} render={<Card />} />
		<Route path={`${path}/collapse`} render={<Collapse />} />
		<Route path={`${path}/dropdown`} render={<Dropdown />} />
		<Route path={`${path}/forms`} render={<Forms />} /> 
		<Route path={`${path}/input-group`} render={<InputGroup />} />
		<Route path={`${path}/modal`} render={<Modal />} />
		<Route path={`${path}/progressbar`} render={<ProgressBar />} />
		<Route path={`${path}/navs`} render={<Navs />} />
		<Route path={`${path}/shadows`} render={<Freestyle />} />
		<Route path={`${path}/table`} render={<Table />} />
		<Route path={`${path}/utilities`} render={<Utilities />} />
		<RouteNotFound render={<PageNotFound home={url} />} />
	</DocumentationFacade>
);

const NeumorphismPageSideBar = ({ url }) => {
	const { setLocal, getLocal } = useLocal(localkey.general);
	const { color, textColor, textColorIsChange, lightSource } = getLocal();
	const colorHandler = (e) => {
		const value = e.target.value;
		const isDarkColor = isColorDark(value);
		const extraChange = textColorIsChange || {
			textColor: colorShade(
				isDarkColor ? '#fff' : '#555',
				isDarkColor ? -10 : 5
			),
		};
		setLocal((prev) => ({
			...prev,
			color: value,
			...extraChange,
		}));
	};
	const textColorHandler = (e) => {
		const value = e.target.value;
		setLocal((prev) => ({
			...prev,
			textColor: value,
			textColorIsChange: true,
		}));
	};

	return (
		<Sidebar>
			<h6 className='division'>General Settings</h6>
			<div className='d-flex flex-column' style={{ padding: '0 2rem' }}>
				<div
					className={`light-source mb-4`}
					data-light-active={lightSource}
					style={{ borderColor: color }}>
					<div className='light-row'>
						<div
							className='n-flat light top-left'
							onClick={() =>
								setLocal((prev) => ({
									...prev,
									lightSource: 'top-left',
								}))
							}
						/>
						<div
							className='n-flat light top-right'
							onClick={() =>
								setLocal((prev) => ({
									...prev,
									lightSource: 'top-right',
								}))
							}
						/>
					</div>
					<div className='light-row flex-center'>
						<div className='n-flat b-round p-1'>
							<i className='mytabworks icon-bulb' />
						</div>
					</div>
					<div className='light-row'>
						<div
							className='n-flat light bottom-left'
							onClick={() =>
								setLocal((prev) => ({
									...prev,
									lightSource: 'bottom-left',
								}))
							}
						/>
						<div
							className='n-flat light bottom-right'
							onClick={() =>
								setLocal((prev) => ({
									...prev,
									lightSource: 'bottom-right',
								}))
							}
						/>
					</div>
				</div>
				<div>
					<label data-color-changable={true}>Theme color</label>
					<div className='n-input-group mb-3'>
						<input
							type='color'
							value={color}
							onChange={colorHandler}
							className='n-form-control'
						/>
						<input
							type='input'
							value={color}
							onChange={colorHandler}
							className='n-form-control'
							minLength={7}
							maxLength={7}
						/>
					</div>
				</div>
				<div>
					<label data-color-changable={true}>Text color</label>
					<div className='n-input-group mb-4'>
						<input
							type='color'
							value={textColor}
							onChange={textColorHandler}
							className='n-form-control'
						/>
						<input
							type='input'
							value={textColor}
							onChange={textColorHandler}
							className='n-form-control'
							minLength={7}
							maxLength={7}
						/>
					</div>
				</div>
				<Actions />
			</div>
			<h6 className='division'>Components</h6>
			<Link className='nav-link n-btn' to={`${url}/button`} label={'Button'} />
			<Link className='nav-link n-btn' to={`${url}/card`} label={'Card'} />
			<Link
				className='nav-link n-btn'
				to={`${url}/collapse`}
				label={'Collapse'}
			/>
			<Link className='nav-link n-btn' to={`${url}/dropdown`} label={'Dropdown'} />
			<Link className='nav-link n-btn' to={`${url}/forms`} label={'Forms'} />
			<Link
				className='nav-link n-btn'
				to={`${url}/input-group`}
				label={'Input group'}
			/>
			<Link className='nav-link n-btn' to={`${url}/modal`} label={'Modal'} />
			<Link
				className='nav-link n-btn'
				to={`${url}/progressbar`}
				label={'Progress bar'}
			/>
			<Link className='nav-link n-btn' to={`${url}/navs`} label={'Navs'} />
			<Link
				className='nav-link n-btn'
				to={`${url}/shadows`}
				label={'Shadows'}
			/>
			<Link className='nav-link n-btn' to={`${url}/table`} label={'Table'} />
			<Link className='nav-link n-btn' to={`${url}/utilities`} label={'Utilities'} />
		</Sidebar>
	);
};

export default NeumorphismPage
